import Taro from '@tarojs/taro'

export interface Location {
  latitude: number
  longitude: number
}

class LocationService {
  // Ëé∑ÂèñÁî®Êà∑ÂΩìÂâç‰ΩçÁΩÆ
  async getCurrentLocation(): Promise<Location> {
    try {
      // Ê£ÄÊü•ÊéàÊùÉÁä∂ÊÄÅ
      const { authSetting } = await Taro.getSetting()
      
      if (!authSetting['scope.userLocation']) {
        // ËØ∑Ê±ÇÊéàÊùÉ
        await Taro.authorize({
          scope: 'scope.userLocation'
        })
      }
      
      // Ëé∑Âèñ‰ΩçÁΩÆ
      const res = await Taro.getLocation({
        type: 'gcj02', // ËøîÂõûÂèØÁî®‰∫é openLocation ÁöÑÂùêÊ†á
        isHighAccuracy: true // ÂºÄÂêØÈ´òÁ≤æÂ∫¶
      })
      
      return {
        latitude: res.latitude,
        longitude: res.longitude
      }
    } catch (error) {
      console.error('Ëé∑Âèñ‰ΩçÁΩÆÂ§±Ë¥•:', error)
      
      // Â¶ÇÊûúÁî®Êà∑ÊãíÁªùÊéàÊùÉÔºå‰ΩøÁî®ÈªòËÆ§‰ΩçÁΩÆÔºà‰∏äÊµ∑Â∏Ç‰∏≠ÂøÉÔºâ
      if ((error as any)?.errMsg?.includes('auth deny')) {
        Taro.showModal({
          title: 'ÊèêÁ§∫',
          content: 'ÈúÄË¶ÅËé∑ÂèñÊÇ®ÁöÑ‰ΩçÁΩÆ‰ø°ÊÅØÊù•Êé®ËçêÈôÑËøëÈó®Â∫ó',
          confirmText: 'ÂéªËÆæÁΩÆ',
          success: (res) => {
            if (res.confirm) {
              Taro.openSetting()
            }
          }
        })
        
        // ËøîÂõûÈªòËÆ§‰ΩçÁΩÆ
        return {
          latitude: 31.2304,
          longitude: 121.4737
        }
      }
      
      throw error
    }
  }
  
  // ËÆ°ÁÆó‰∏§ÁÇπ‰πãÈó¥ÁöÑË∑ùÁ¶ªÔºàÂçï‰ΩçÔºöÂÖ¨ÈáåÔºâ
  calculateDistance(
    lat1: number,
    lng1: number,
    lat2: number,
    lng2: number
  ): number {
    const rad = Math.PI / 180
    const R = 6371 // Âú∞ÁêÉÂçäÂæÑÔºàÂÖ¨ÈáåÔºâ
    
    const dLat = (lat2 - lat1) * rad
    const dLng = (lng2 - lng1) * rad
    
    const a = 
      Math.sin(dLat / 2) * Math.sin(dLat / 2) +
      Math.cos(lat1 * rad) * Math.cos(lat2 * rad) *
      Math.sin(dLng / 2) * Math.sin(dLng / 2)
      
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a))
    
    return Number((R * c).toFixed(1))
  }
  
  // Ê†ºÂºèÂåñË∑ùÁ¶ªÊòæÁ§∫
  formatDistance(distance: number): string {
    if (distance < 1) {
      return `${Math.round(distance * 1000)}m`
    }
    return `${distance}km`
  }
}

export const getLocationService = new LocationService()